@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@200;300;400;500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  margin: 0;
  max-width: 100vw;
  font-family: "Montserrat", sans-serif;
  overflow-x: hidden;
}
.footer-tab {
  background-color: #121d12c7;
  color: rgb(187, 193, 199);
  padding: 2rem 1rem;
  font-size: 12px;
  text-align: center;
  margin-top: 4rem;
}

.navBar {
  width: 100%;
  background-color: #fff;
  color: #000;
  padding: 10px;
  position: relative;
}
.ncerdlogo {
  height: 3.5rem;
  width: 5rem;
  z-index: 1;
  margin-right: 2rem;
  border-radius: 3rem;
}
.navItems {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}
.links {
  height: 0;
  overflow: hidden;
  list-style-type: none;
  font-weight: bold;
  text-align: center;
  position: absolute;
  transition: all 0.5s;
  margin-right: 12%;
  margin-top: 3.5rem;
  margin-left: -12%;
  width: 100%;
  background-color: #fff;
}
.nav-list {
  padding: 1rem 0;
}

.nav-link {
  color: #000000;
}
.nav-link:link {
  text-decoration: none;
}
.show-nav {
  text-align: center;
  margin-left: -12%;
  margin-right: 12%;
  margin-top: 3.5rem;
  width: 102.5%;
  height: 16rem;
  border-bottom: 4px solid #739a82;
  background-color: #fff;
  position: fixed;
  z-index: 2;
}
.nav-icon {
  font-size: 30px;
  margin-right: 0;
  color: #334238;
  margin-top: 0.8rem;
  background: #fff;
  border: none;
  position: fixed;
  right: 1%;
  top: 1%;
  cursor: pointer;
  outline: none;
  z-index: 1;
  float: right;
}
.nav-border {
  border: 4px solid #739a82;
  margin: 0 -1rem;
}

/* Media screen size */
@media screen and (min-width: 768px) {
  .ncerdlogo {
    margin-right: 2rem;
  }
  .nav-icon {
    display: none;
  }

  .links {
    height: auto;
    display: -webkit-flex;
    display: flex;
    background-color: rgba(255, 255, 255, 0);
    margin-top: 1.5rem;
    border-bottom: transparent;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
  }
  .nav-list {
    padding: 0 15px;
  }
}

.card {
  height: 25rem;
  -webkit-align-items: center;
          align-items: center;
  overflow-x: hidden;
}
.card-con {
  margin: 0.5rem;
  height: 25rem;
  overflow-y: hidden;
}
.home-page {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}
.main-sec {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}
.sec-bit {
  width: 85%;
  margin: 1rem;
  padding: 0.5rem;
}
.project-list {
  text-align: left;
  list-style-type: none;
  padding: 0;
}
.project-list > li {
  margin: 0.5rem auto;
  border-left: 4px solid #587061;
  transition: all 0.5s;
}
.project-list > li:hover {
  background-color: #abccb852;
}
.home-button {
  height: 2.5rem;
  width: 7rem;
  border-radius: 10px;
  border: none;
  color: #fff;
  transition: all 0.4s;
  background-color: #587061;
}
.home-button:hover {
  font-size: 0.9rem;
  background-color: #50976b;
}
.carousel-card2 {
  background-size: contain;
  background: linear-gradient(
      0.23deg,
      rgba(0, 0, 0, 0.88) 8.79%,
      rgba(0, 0, 0, 0.06) 66.99%
    ),
    url(/ncerd-unn-web/static/media/lllk.2e7f1516.png) repeat;
}
.carousel-card1 {
  background: linear-gradient(
      0.23deg,
      rgba(0, 0, 0, 0.88) 8.79%,
      rgba(0, 0, 0, 0.06) 66.99%
    ),
    url(/ncerd-unn-web/static/media/9FB59627-471B-4449-87AF-9D388A07DB2E.029551b3.JPEG) repeat;
}
.carousel-card3 {
  background-size: contain;
  background: linear-gradient(
      0.23deg,
      rgba(0, 0, 0, 0.88) 8.79%,
      rgba(0, 0, 0, 0.06) 66.99%
    ),
    url(/ncerd-unn-web/static/media/slide.2be82826.jpeg) no-repeat;
}
.carousel-card4 {
  background-size: contain;
  background: linear-gradient(
      0.23deg,
      rgba(0, 0, 0, 0.88) 8.79%,
      rgba(0, 0, 0, 0.06) 66.99%
    ),
    url(/ncerd-unn-web/static/media/slide01.fb1d6cf7.jpeg) no-repeat;
}
.slick-dots {
  position: relative;
  right: 0;
  bottom: 5;
  left: 0;
  z-index: 15;
  display: -webkit-flex !important;
  display: flex !important;
  -webkit-justify-content: center;
          justify-content: center;
  margin-right: 15%;
  margin-top: -7%;
  margin-left: 15%;
  height: 1.5px;
  padding-left: 0;
  list-style: none;
}
/* Media screen size */
@media screen and (min-width: 768px) {
  .main-sec {
    -webkit-flex-direction: row;
            flex-direction: row;
  }
  .sec-bit {
    width: 33.333%;
  }
  .card {
    width: 40rem;
  }
  .card-con {
    width: 80%;
    margin: 0.5rem auto;
  }
  .carousel-card1 {
    background: linear-gradient(
        0.23deg,
        rgba(0, 0, 0, 0.88) 8.79%,
        rgba(0, 0, 0, 0.06) 66.99%
      ),
      url(/ncerd-unn-web/static/media/B415140D-39D6-4C1D-A638-29F5A3C53637.e1e2d794.JPEG) repeat;
  }
}

.projects-page {
  padding: 0.5rem;
}
.legend {
  display: -webkit-flex;
  display: flex;
}
.legend > h5 {
  margin-right: 1rem;
  padding-left: 0.5rem;
}
#project-done {
  border-left: 4px solid #587061;
}
#project-on {
  border-left: 4px solid #ffa500;
}
.main-project-list > li {
  padding: 1rem auto;
  margin: 1.5rem auto;
}

.contact-section {
  display: -webkit-flex;
  display: flex;
  margin-bottom: 2.7rem;
  -webkit-flex-direction: column;
          flex-direction: column;
}
.contact-subject {
  display: -webkit-flex;
  display: flex;
}
.contact-card {
  border: 0.5px solid #3f3e3e73;
  margin: 1rem;
  border-radius: 5px;
  display: -webkit-flex;
  display: flex;
  padding: 1rem;
  -webkit-flex-direction: column;
          flex-direction: column;
}
.c-h {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}
.icon-con {
  margin-right: 1.5rem;
}
.hide-info {
  display: none;
}
/* Media screen size */
@media screen and (min-width: 768px) {
  .contact-section {
    -webkit-flex-direction: row;
            flex-direction: row;
    text-align: center;
    margin-bottom: 6.2rem;
  }
  .hide-info {
    display: contents;
  }
  .contact-card {
    width: 33.333%;
    -webkit-justify-content: center;
            justify-content: center;
  }
  .c-h {
    -webkit-justify-content: center;
            justify-content: center;
  }
  .contact-subject {
    font-size: 1.2rem;
    -webkit-flex-direction: column;
            flex-direction: column;
  }
  .exp-icon {
    display: none;
  }
  .icon-con {
    margin-right: 0;
    margin-bottom: 0.5rem;
    background-color: #8a969279;
    border-radius: 40%;
    height: 2.5rem;
    width: 2.5rem;
    font-size: 2rem;
    padding-top: 10%;
    -webkit-align-self: center;
            align-self: center;
  }
}

@media screen and (min-width: 768px) {
  .about-sec {
    -webkit-flex-direction: column;
            flex-direction: column;
    margin: 0 auto;
  }
  .a-sec-bit {
    width: 94%;
  }
}
.a-sec-bit > ul > li,
.a-sec-bit > ol > li {
  margin: 1rem 0;
}
li,
p {
  color: #1b1b1ba1;
}
.a-sec-bit > h5 {
  color: #1b1b1ba1;
}

